.container{
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 50px;
    width: 80%;
    margin: 0 auto;
}

.poster{
    border-radius: 20px;
    max-width: 80%;
    place-self: center;
}

.title{
    color: white;
    font-size: 36;
    font-weight: 600;
}

.info{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    gap: 20px;
}

